/* dla menu schowane gdy schowane*/
span.s-one.go {
  -webkit-transform: translateY(10px) rotate(-45deg);
          transform: translateY(10px) rotate(-45deg);
  -webkit-animation: go-one .7s ease-in-out;
          animation: go-one .7s ease-in-out;
}

span.s-two.go {
  -webkit-transform: translateY(0) rotate(0);
          transform: translateY(0) rotate(0);
  width: 0px;
  -webkit-animation: go-two .7s ease-in-out;
          animation: go-two .7s ease-in-out;
}

span.s-three.go {
  -webkit-transform: translateY(-10px) rotate(45deg);
          transform: translateY(-10px) rotate(45deg);
  -webkit-animation: go-three .7s ease-in-out;
          animation: go-three .7s ease-in-out;
}

@-webkit-keyframes go-one {
  0% {
    -webkit-transform: translateY(0) rotate(0);
            transform: translateY(0) rotate(0);
  }

  40% {
    -webkit-transform: translateY(10px) rotate(0);
            transform: translateY(10px) rotate(0);
  }

  70% {
    -webkit-transform: translateY(10px) translateX(0) rotate(-45deg);
            transform: translateY(10px) translateX(0) rotate(-45deg)
  }

  100% {
    -webkit-transform: translateY(10px) rotate(-45deg);
            transform: translateY(10px) rotate(-45deg);
  }
}

@keyframes go-one {
  0% {
    -webkit-transform: translateY(0) rotate(0);
            transform: translateY(0) rotate(0);
  }

  40% {
    -webkit-transform: translateY(10px) rotate(0);
            transform: translateY(10px) rotate(0);
  }

  70% {
    -webkit-transform: translateY(10px) translateX(0) rotate(-45deg);
            transform: translateY(10px) translateX(0) rotate(-45deg)
  }

  100% {
    -webkit-transform: translateY(10px) rotate(-45deg);
            transform: translateY(10px) rotate(-45deg);
  }
}

@-webkit-keyframes go-two {
  0% {
    width: 30px;
  }

  40% {
    width: 30px;
  }

  40.1% {
    width: 0;
  }

  100% {
    width: 0;
  }
}

@keyframes go-two {
