.error {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  letter-spacing: 1.5px;
  background-image: linear-gradient(rgb(65, 56, 51) 15%, rgb(192, 190, 194));
  overflow: hidden;
}

